import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import NameForm from './components/NameForm';
import HelloLuoxinguo from './components/HelloLuoxinguo';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>
            <img src={logo} className="App-logo" alt="logo" />
            
          </h1>
          <address>Edit <code>src/App.js</code> and save to reload.</address>
          <NameForm/>
          {/*<a*/}
            {/*className="App-link"*/}
            {/*href="https://reactjs.org"*/}
            {/*target="_blank"*/}
            {/*rel="noopener noreferrer"*/}
          {/*>*/}
            {/*Learn React*/}
          {/*</a>*/}
          <HelloLuoxinguo/>
        </header>
      </div>
    );
  }
}

export default App;
