import React from 'react';
import mousepng from '../img/Cat.png'

class Cat extends React.Component {
  constructor(props){
    super(props);
  }
  
  render(){
    const mouse = this.props.mouse;
    return (
      <img src={mousepng} alt="老鼠随猫动" style={{ position: 'absolute',  left: mouse.x-740, top: mouse.y-200 }}/>
    );
  }
}

class Mouse extends React.Component {
  constructor(props) {
    super(props);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.state = { x: 0, y: 0 };
  }
  
  handleMouseMove(event) {
    this.setState({
      x: event.clientX,
      y: event.clientY,
    });
  }
  
  render() {
    return (
      <div  style={{ height: '100vh', position: 'relative', border: '1px solid #396'}}  onMouseMove={this.handleMouseMove}>
        {/*
          Instead of providing a static representation of what <Mouse> renders,
          use the `render` prop to dynamically determine what to render.
        */}
        {this.props.render(this.state)}
      </div>
    );
  }
}

class MouseTracker extends React.Component {
  constructor(props){
    super(props);
  }
  
  renderTheCat (mouse) {
    return <Cat mouse={mouse} />;
  }
  
  render() {
    return (
      <div>
        <h1>移动鼠标!</h1>
        <Mouse   render={this.renderTheCat}/>
      </div>
    );
  }
}

export {
  Mouse,
  MouseTracker
}