import React from 'react';
import '../css/helloluoxinguo.css';
import {
  maopao,
  gaijinmaopao,
  selectionSort,
  insertionSort,
  mergeSort,
  quickSort,
  heapSort,
  countingSort,
} from '../tools/algorithm.js'
import { MouseTracker } from './Mouse'

//try React Syntax
function formatName(user){
  return user.familyname + ' ' + user.name;
}
const name = '肖 宗杰'.split(' ');
const user = {
  familyname: name[0],
  name: name[1]
};

const array =[3,44,38,5,47,15,36,26,44,26,27,2,46,4,19,50,48];

class HelloLuoxinguo extends React.Component {
  render(){
    return (
      <h1 className='luoxinguo'>
        Hello, {formatName(user)}!
        <p>
          {
            countingSort(array).map((elem, index) =>
              <span key={index}>{elem}, </span>
            )
          }
        </p>
        <MouseTracker/>
      </h1>
    );
  }
}
export default HelloLuoxinguo;

